.gform_wrapper form .gfield_description ol li {
  list-style-type: decimal !important; }

.gform_wrapper form .gfield_description ol.alpha li {
  list-style-type: lower-alpha !important; }

@keyframes gscr-button-flash {
  0% {
    color: #4e0b55; }
  89% {
    color: #4e0b55; }
  90% {
    color: #a117af; }
  91% {
    color: #4e0b55; }
  92% {
    color: #a117af; }
  93% {
    color: #4e0b55; }
  100% {
    color: #4e0b55; } }

.sticky-container .sticky {
  width: 100%; }
  .sticky-container .sticky, .sticky-container .sticky * {
    transition: all 300ms; }

@media only screen and (max-width: 733px) and (orientation: landscape), only screen and (min-width: 40.063em) and (max-width: 64em) and (orientation: portrait) {
  .sticky-container.show-for-medium {
    display: none !important; }
  .sticky-container.hide-for-medium {
    display: inline-block !important;
    width: 100%; }
    .sticky-container.hide-for-medium .jp-audio-stream {
      width: 100% !important; }
      .sticky-container.hide-for-medium .jp-audio-stream.is-at-bottom {
        bottom: -0.65em !important; } }

.sticky-container .jp-audio-stream {
  padding: 15px;
  width: 100%;
  background: #fff;
  border-top: 1px solid #0155a5; }
  .sticky-container .jp-audio-stream.is-anchored {
    top: auto !important; }
  .sticky-container .jp-audio-stream button:focus {
    outline: none; }
  .sticky-container .jp-audio-stream .fa {
    color: #4e0b55; }
    .sticky-container .jp-audio-stream .fa:hover {
      cursor: pointer; }
  .sticky-container .jp-audio-stream .jp-play .play-icon {
    animation: gscr-button-flash 10s infinite;
    transition: 300ms all; }
  .sticky-container .jp-audio-stream .jp-play .play-icon:before {
    content: '\f144'; }
  .sticky-container .jp-audio-stream .jp-play:hover .play-icon:before {
    content: '\f01d'; }
  .sticky-container .jp-audio-stream.jp-state-paused .jp-play .play-icon:before {
    content: '\f144'; }
  .sticky-container .jp-audio-stream.jp-state-paused .jp-play:hover .play-icon:before {
    content: '\f01d'; }
  .sticky-container .jp-audio-stream.jp-state-playing .jp-play .play-icon:before {
    content: '\f28b'; }
  .sticky-container .jp-audio-stream.jp-state-playing .jp-play:hover .play-icon:before {
    content: '\f28c'; }
  .sticky-container .jp-audio-stream .title-container {
    position: relative; }
    .sticky-container .jp-audio-stream .title-container .jp-title {
      position: absolute;
      transform: translate(0%, 50%);
      width: 100%; }
  .sticky-container .jp-audio-stream .jp-mute .mute-icon {
    color: #e6e6e6; }
    .sticky-container .jp-audio-stream .jp-mute .mute-icon:before {
      content: '\f026'; }
  .sticky-container .jp-audio-stream.jp-state-muted .jp-mute .mute-icon {
    color: #4e0b55; }
  .sticky-container .jp-audio-stream .jp-volume-controls .volume-bar-container {
    position: relative; }
    .sticky-container .jp-audio-stream .jp-volume-controls .volume-bar-container .jp-volume-bar {
      overflow: hidden;
      background-color: #e6e6e6;
      height: 1em;
      cursor: pointer;
      position: absolute;
      transform: translate(2%, 100%);
      width: 100%; }
    .sticky-container .jp-audio-stream .jp-volume-controls .volume-bar-container .jp-volume-bar-value {
      background-color: #4e0b55;
      width: 0px;
      height: 1em; }
  .sticky-container .jp-audio-stream .jp-volume-max .volume-max-icon {
    color: #e6e6e6; }
    .sticky-container .jp-audio-stream .jp-volume-max .volume-max-icon:before {
      content: '\f028'; }
  .sticky-container .jp-audio-stream .jp-volume-max:focus .volume-max-icon {
    color: #4e0b55; }

.show-for-print-only {
  display: none;
  visibility: hidden; }

@media print {
  .show-for-print-only {
    display: inline-block;
    visibility: visible; }
  .hide-for-print {
    display: none;
    visibility: hidden; }
  a[href]:after {
    content: none !important; }
  .tabs-panel {
    display: inline-block; } }
